import { useState } from 'react';
import { BrowserRouter, Routes, Route, useParams } from 'react-router-dom'
import { Home } from './homePage/home';
import './index.css'
import { store } from './redux/store';
import { Shop } from './shopPage/shop';
import { CateKitchen, CateLiving } from './category/category';
import { AddError } from './redux/reducers/errorReducer';
import { ViewCart } from './checkout/viewCart';
import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..
AOS.init();




export function App() {
  
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path='/shop' element={<Shop />} />
        <Route path='/viewCart' element={<ViewCart />} />
        <Route path='/category/Living%20Room/:know' element={<CateLiving />} />
        <Route path='/category/Kitchen/:know' element={<CateKitchen />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;



export function Fetch(obj) {
  const first = async (dispatch, getState) => {
    try {
      const p = await fetch(obj.url);
      const res = await p.json();
      await dispatch(obj.action({ prod: res, know: obj.know }))
      dispatch(obj.statusFunc({know:obj.know}))
    } catch (error) {
      
      
      dispatch(AddError(true))
    }
  }

  store.dispatch(first)
}