import { useEffect, useState } from "react";
import { Navbar } from "../commonFunction/navbar";
import { store } from "../redux/store";
import './shop.css'
import { Fetch } from '../App'
import { Products } from "./product";
import { firstCall, status } from "../redux/reducers/productsReducer";
import { ProductSider } from "./ProductSider";
import { Footer } from "../commonFunction/footer";



// storing the state so that i dont need to update products again and again;
var cashe;



export function Shop() {
    // first it shows loading and set all hooks to be there when the data arrived
    var stateTobeSet = [];
    for (var i = 0; i < store.getState().prod.lengths.all; i++) {
        stateTobeSet[i] = i;
    }
    // checking that i already have data or not
    if (store.getState().prod.firstCall.status) {
        stateTobeSet = store.getState().prod.firstCall.data
    }
    var [prod, prodFunc] = useState(stateTobeSet);
    var [error, errorFunc] = useState(false)
    useEffect(() => {
        if (store.getState().prod.lengths.all !== store.getState().prod.firstCall.data.length) {
            Fetch({
                url: 'http://localhost:8000/stock/shop',
                action: firstCall,
                know: 'firstCall',
                statusFunc: status,
            })
            store.subscribe(() => {

                if (store.getState().prod.firstCall !== cashe) {
                    cashe = store.getState().prod.firstCall.data

                    if (store.getState().prod.firstCall.status) {
                        var data = store.getState().prod.firstCall.data
                        prodFunc(data)
                    } else {
                        errorFunc(store.getState().error.shop)
                    }


                }
            })
        }
    }, [])

    return (
        <>
            <Navbar />
            <div className="container-fluid bg-home-img text-center">
                <h1 className="display-3 font-serif text-light">Shop</h1>
            </div>
            <div className="container-fluid px-2 mt-5 mb-5">
                <div className="row w-100">
                    <div className="col px-0">
                        <ProductSider />
                    </div>
                    <div className="col-9 sans">
                        {
                            (error) ?
                                <div className="container ">
                                    <h1>Can't get products</h1>
                                    <h3>Try:</h3>
                                    <ul className="ms-3">
                                        <li>Checking the network cables, modem and router</li>
                                        <li>Reconnecting to Wi-Fi</li>
                                    </ul>
                                </div>
                                :
                                <>
                                    <div className="container-fluid px-1">
                                        <div className="row pb-3 ">
                                            <div className="col">
                                                <span
                                                    className="text-md d-inline font-serif-regular">
                                                    Showing 1–20 of 62 results
                                                </span>
                                            </div>
                                            <div className="col position-relative">
                                                <select
                                                    className="text-md position-absolute end-0 form-select w-50">
                                                    <option value="">Sort by popularity</option>
                                                    <option value="">Sort by average rating</option>
                                                    <option value="">Sort by latest</option>
                                                    <option value="">Sort by price: low to high</option>
                                                    <option value="">Sort by price: high to low</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row row-cols-4 row-cols-xxl-5 mt-4">
                                        {<Products put={prod} />}
                                    </div>
                                </>
                        }
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}

