import React, { useState } from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { store } from './redux/store';
import { firstCall, lengths } from './redux/reducers/productsReducer'


function first() {
    const root = ReactDOM.createRoot(document.getElementById('root'));
    root.render(
        <App />
    );
}


fetch('http://localhost:8000/know/length')
.then((json)=>json.json())
.then((res)=>{
    store.dispatch(lengths(res))
    first();
})




