import { useEffect, useState } from "react";
import { store } from "../redux/store";
import { Add, Sub } from "../redux/reducers/cartReducer";
import { LoadingFunc } from "../commonFunction/loading";




export function Products(props) {
    var [btn, btnFunc] = useState([])
    var [count, countFunc] = useState([]);
    useEffect(() => {
        var trues = [];
        var counts = [];
        props.put.forEach((value, index) => {
            trues[index] = true;
            counts.push(1)
        })
        countFunc(counts)
        btnFunc(trues)
    }, [])
    // useEffect(() => {
    //     var put = store.getState().cart;
    //     const know = put.find(value => value._id === get._id)
    //     if (know !== undefined) {
    //         btnFunc(false);
    //         countFunc(know.quant);
    //     }
    // }, [get])
    // useEffect(() => {
    //     if (get._id !== undefined) {
    //         store.subscribe(() => {
    //             var cart = store.getState().cart

    //             const know = cart.find(value => value._id === get._id);

    //             if (know === undefined) {
    //                 btnFunc(true);
    //                 countFunc(1);
    //             }
    //         })
    //     }

    // }, [get])
    function chng(get) {
        // store.dispatch(Add(get))
        var arr = [];
        props.put.forEach((value, index) => {
            if (value._id === get) {
                arr[index] = false
            } else {
                arr[index] = btn[index]
            }
        })
        btnFunc(arr);
        // btnFunc(false)
    }
    function add(get) {
        // store.dispatch(Add(get))
        var arr = [];
        var counts = [];
        props.put.forEach((value, index) => {
            if (value._id === get) {
                counts[index] = count[index] + 1
            } else {
                counts[index] = count[index]
            }
        })
        countFunc(counts)
        // countFunc(count + 1);
    }
    function sub(get) {
        // store.dispatch(Sub(get))
        var arr = [];
        var counts = [];
        props.put.forEach((value, index) => {
            if (value._id === get) {
                if (count[index] === 1) {
                    arr[index] = true;
                    counts[index] = 1
                } else {
                    arr[index] = false
                    counts[index] = count[index] - 1
                }
            } else {
                arr[index] = btn[index]
                counts[index] = count[index]
            }
        })
        countFunc(counts)
        btnFunc(arr);
    }
    function InsProd(get, index) {
        // if (typeof (get) === 'number') {
        //     return LoadingFunc(get)
        // }
        return (
            <div className="col px-1" key={get._id}>
                <div className="card px-0 h-100" >
                    <img src={get.filename} className="card-img-top" alt="..." />
                    <div className="card-body">
                        <p className="card-text mb-1 font-serif-regular">{get.name}</p>
                        <p className="fw-bold mb-1">${get.price}</p>
                        <p className="text-success text-md">2 Day Delivery</p>
                        <button className="btn btn-light w-100 h-25px invisible"></button>
                        {
                            (btn[index]) ?
                                <button className="btn btn-primary h-25px position-absolute card-btn-manage"
                                    onClick={() => { chng(get._id) }}
                                >
                                    Add to Cart</button>
                                :
                                <div className="border three-btn h-25px position-absolute card-btn-manage">
                                    <button className="w-25 btn border-end" onClick={() => { sub(get._id) }}>
                                        -
                                    </button>
                                    <button className="btn border-end w-50">
                                        {count[index]}
                                    </button>
                                    <button className="w-25 btn" onClick={() => { add(get._id) }}>
                                        +
                                    </button>
                                </div>
                        }
                    </div>
                </div>
            </div>
        )
    }
    return props.put.map((value, index) => InsProd(value, index))
}
