import { useState } from "react"

const arr = [
    'Living Room',
    'Bedroom',
    'Business Solution',
    'Home Office',
    'Lighting',
    'Home Decoration',
    'Dining Room',
    'Kitchen',
    'Nursery & Kids',
    'Bathroom',
    'Hallway',
    'Outdoor'
]

const color = [
    'Black',
    'Blue',
    'Brown',
    'Gray',
    'Green',
    'Orange',
    'Red'
]



export function ProductSider() {
    return (
        <div className="container-fluid mt-2">
            <div className="col">
                <h6 className="font-serif mb-3">Products Categories</h6>
                {arr.map(Col)}
                <h6 className="font-serif mb-3 mt-5">Filter by price</h6>
                <PriceFilter />
                <h6 className="font-serif mb-3 mt-5">Filter by color</h6>
                {color.map(ColorFilter)}
                <img src="img/shop/sidebar-banner.jpg" className="w-100 mt-5" alt="" />
            </div>
        </div>
    )
}

function Col(props) {
    return (
        <div className="col-12 font-serif-regular mt-2 text-md pe-3" key={props}>

            <div className="row">
                <div className="col">
                    <span className="ms-3">
                        {props}
                    </span>
                </div>
                <div className="col-1">
                    <input type="checkbox" />
                </div>
            </div>

        </div>
    )
}

function PriceFilter() {
    
    return (
        <div className="row mt-4 pe-3">
            <div className="col pe-0">
                <input type="number" className="form-control font-serif-regular" placeholder="Min" />
            </div>
            <div className="col-1 mt-1">
                -
            </div>
            <div className="col ps-0">
                <input type="number" className="form-control font-serif-regular" placeholder="Max" />
            </div>
            <div className="col-1 pt-1 bg-lgrey rounded-3 text-center px-0">
                <i className="fa-solid fa-angle-right position-relative top-2"></i>
            </div>
        </div>
    )
}

function ColorFilter(props){
    var [show,func] = useState('invisible')
    function chng(){
        if(show==='invisible'){
            func('')
        }else{
            func('invisible')
        }
    }
    return (
        <div className="col-12 font-serif-regular mt-2 text-md pointer pe-3" onClick={chng} key={props}>

            <div className="row">
                <div className="col-1 pe-0 position-relative ps-0 ms-2 mb-0">
                <i 
                    className={`fa-solid fa-check position-absolute top-2 start-2 text-light fa-md ${show}`}>

                </i>
                    <div className="circle-color " style={{backgroundColor:props}}></div>
                </div>
                <div className="col ps-0">
                    <span className="ms-1">
                        {props}
                    </span>
                </div>
                <div className="col-1 text-secondary pe-3">
                    (18)
                </div>
            </div>

        </div>
    )
}