import { useState } from "react"

const arr = [
    'Shoaib Bhai',
    'Royal Badu',
    'Sameer Choudhary',
    'Suhail Su'
]





export function Testimonial() {
    var [opac, func] = useState(['', 'op-5'])
    var [slide,slideFunc]=useState('');
    function chng(know) {
        if (know === 1) {
            func(['op-5', ''])
            slideFunc('slide-x')
        } else {
            func(['', 'op-5'])
            slideFunc('slidex')
        }
    }
    return (
        <div className="container mb-5 overflow-hidden text-center">
            <h1 className='font-serif text-ddark mt-5'>Our Lucky Customer</h1>
            <p className='cursive text-secondary mb-5 text-md'>
                Visit our shop to see amazing creations from our designers.
            </p>
            <div className={`row ${slide}`} style={{ width: '136%' }}>
                {arr.map(Col)}
            </div>
            <div className="mt-3">
                <span
                    className={`circle position-relative bg-dark ${opac[0]}`}
                    onClick={chng}
                >
                </span>
                <span
                    className={`circle position-relative bg-dark ms-4 ${opac[1]}`}
                    onClick={() => { chng(1) }}
                >
                </span>
            </div>
        </div>
    )
}


function Col(get) {
    return (
        <div className="col text-center" key={get}>
            <div className="col-12 border p-3">
                <h5 className="font-serif ">{get}</h5>
                <p className="text-md">
                    ⭐⭐⭐⭐⭐
                </p>
                <p className="text-sm font-serif-gegular text-secondary">
                    There are many variations of passages of Lorem Ipsum available,
                    but the majority have suffered alteration in some form, by
                    injected humour, or randomised words which don't look even slightly
                    believable.
                </p>
            </div>
        </div>
    )
}