import { featCate, status } from '../redux/reducers/productsReducer';
import { Products } from '../shopPage/product';
import { useState, useEffect } from 'react';
import { store } from '../redux/store';
import { Fetch } from '../App';



// storing the state so that i dont need to update products again and again;
var cashe;



export function FeatCate(get) {
    // first it shows loading and set all hooks should be there when the data arrived
    var stateTobeSet = [];
    for (var i = 0; i < store.getState().prod.lengths.all; i++) {
        stateTobeSet[i] = i;
    }

    // checking that i already have data or not
    if (store.getState().prod.featCate.status) {
        stateTobeSet = store.getState().prod.featCate.data
    }

    var [prod, prodFunc] = useState(stateTobeSet);
    useEffect(() => {
        if (store.getState().prod.lengths.all !== store.getState().prod.featCate.data.length) {
            Fetch({
                url: 'http://localhost:8000/stock/feat',
                action: featCate,
                stats: 'featCate',
                statusFunc: status
            })
            store.subscribe(() => {
                if (store.getState().prod.featCate.data !== cashe) {
                    cashe = store.getState().prod.featCate.data

                    prodFunc(store.getState().prod.featCate.data)
                }
            })
        }
    }, [])
    return (
        <div className="container mb-5  text-center">
            <h1 className='font-serif text-ddark mt-5' data-aos="fade-up">Featured Categories</h1>
            <p className='cursive text-secondary mb-5 text-md' data-aos="fade-up">
                Visit our shop to see amazing creations from our designers.
            </p>
            <div className="row row-cols-xl-5 row-cols-lg-4">
                {<Products put={prod} />}
            </div>
        </div>
    )
}