import { useParams, Link } from "react-router-dom"
import { store } from "../redux/store";
import { useEffect, useState } from "react";
import { Fetch } from "../App";
import { cateStatus } from "../redux/reducers/categoryReducer";
import { cate } from "../redux/reducers/categoryReducer";
import { Navbar } from "../commonFunction/navbar";
import { Footer } from "../commonFunction/footer";
import { Products } from "../shopPage/product";

const color = [
    'Black',
    'Blue',
    'Brown',
    'Gray',
    'Green',
    'Orange',
    'Red'
]
const arr = [
    { src: 'livingRoom.png', title: 'Living Room' },
    { src: 'Bedroom.png', title: 'Bedroom' },
    { src: 'BusinessSolution.png', title: 'Business Solution' },
    { src: 'HomeOffice.png', title: 'Home Office' },
    { src: 'Lighting.png', title: "Lighting" },
    { src: 'HomeDecoration.png', title: 'Home Decoration' }
]

function keyReplacer(know, obj) {
    var noSpaceKnow = know.replace(' ', '');
    var objLength = {
        ...obj
    }
    objLength = JSON.stringify(objLength)
    objLength = objLength.replace(noSpaceKnow, 'know')
    return JSON.parse(objLength)

}

export function CateComp(){
    // finding the category i want
    let { know } = useParams();
    var stateTobeSet = [];
    // normal work as in shop page
    var objLength = keyReplacer(know, store.getState().prod.lengths)
    for (var i = 0; i < objLength.know; i++) {
        stateTobeSet[i] = i;
    }

    // checking that i have products or not
    var data = keyReplacer(know, store.getState().cate)
    if (data.know.status) {
        stateTobeSet = data.know.data
    }
    // all hooks
    var [error, errorFunc] = useState(false)
    var [prod, prodFunc] = useState(stateTobeSet);
    


    useEffect(() => {
        if (objLength.know !== data.know.data.length) {
            Fetch({
                url: 'http://localhost:8000/stock/' + know,
                action: cate,
                know,
                statusFunc: cateStatus,
            })
            store.subscribe(() => {
                var cateB = store.getState().cate;
                var subsData = keyReplacer(know, cateB)
                
                    
                    if (subsData.know.status) {
                        prodFunc(subsData.know.data);
                    } else {
                        errorFunc(store.getState().error.shop)
                    }
                
            })
        }
    }, [know])
    return (
        <>

            <Navbar />
            <div className="container-fluid bg-home-img text-center">
                <h1 className="display-3 font-serif text-light">Category: {know}</h1>
            </div>
            <div className="container mt-5 pt-3">
                <div className="row">
                    {arr.map(Col)}
                </div>
            </div>
            <hr className="bg-secondary" />
            <div className="container-fluid px-2 mt-5 mb-5">
                <div className="row w-100">
                    <div className="col px-0">
                        <CateSider />
                    </div>
                    <div className="col-9 sans">
                        {
                            (error) ?
                                <div className="container ">
                                    <h1>Can't get products</h1>
                                    <h3>Try:</h3>
                                    <ul className="ms-3">
                                        <li>Checking the network cables, modem and router</li>
                                        <li>Reconnecting to Wi-Fi</li>
                                    </ul>
                                </div>
                                :
                                <>
                                    <div className="container-fluid px-1">
                                        <div className="row pb-3 ">
                                            <div className="col">
                                                <span
                                                    className="text-md d-inline font-serif-regular">
                                                    Showing 1–20 of 62 results
                                                </span>
                                            </div>
                                            <div className="col position-relative">
                                                <select
                                                    className="text-md position-absolute end-0 form-select w-50">
                                                    <option value="">Sort by popularity</option>
                                                    <option value="">Sort by average rating</option>
                                                    <option value="">Sort by latest</option>
                                                    <option value="">Sort by price: low to high</option>
                                                    <option value="">Sort by price: high to low</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row row-cols-4 row-cols-xxl-5 mt-4">
                                        {<Products put={prod} />}
                                    </div>
                                </>
                        }
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}


function PriceFilter() {
    return (
        <div className="row mt-4 pe-3">
            <div className="col pe-0">
                <input type="number" className="form-control font-serif-regular" placeholder="Min" />
            </div>
            <div className="col-1 mt-1">
                -
            </div>
            <div className="col ps-0">
                <input type="number" className="form-control font-serif-regular" placeholder="Max" />
            </div>
            <div className="col-1 pt-1 bg-lgrey rounded-3 text-center px-0">
                <i className="fa-solid fa-angle-right position-relative top-2"></i>
            </div>
        </div>
    )
}

function ColorFilter(props) {
    var [show, func] = useState('invisible')
    function chng() {
        if (show === 'invisible') {
            func('')
        } else {
            func('invisible')
        }
    }
    return (
        <div className="col-12 font-serif-regular mt-2 text-md pointer pe-3" onClick={chng} key={props}>

            <div className="row">
                <div className="col-1 pe-0 position-relative ps-0 ms-2 mb-0">
                    <i
                        className={`fa-solid fa-check position-absolute top-2 start-2 text-light fa-md ${show}`}>

                    </i>
                    <div className="circle-color " style={{ backgroundColor: props }}></div>
                </div>
                <div className="col ps-0">
                    <span className="ms-1">
                        {props}
                    </span>
                </div>
                <div className="col-1 text-secondary pe-3">
                    (18)
                </div>
            </div>

        </div>
    )
}

function Col(get) {
    return (

        <div className="col text-center" key={get.src}>
            <Link to={'/'} className="text-decoration-none text-dark">
                <img src={`../../img/home/${get.src}`} className="img-center" alt="" />
                <p className="mt-4 font-serif fw-bold text-md">{get.title}</p>
            </Link>
        </div>
    )
}

function CateSider() {
    return (
        <div className="container-fluid mt-2">
            <div className="col">
                <h6 className="font-serif mb-3 mt-5">Filter by price</h6>
                <PriceFilter />
                <h6 className="font-serif mb-3 mt-5">Filter by color</h6>
                {color.map(ColorFilter)}
                <img src="../../img/shop/sidebar-banner.jpg" className="w-100 mt-5" alt="" />
            </div>
        </div>
    )
}