import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Delete } from "../redux/reducers/cartReducer";
import { store } from "../redux/store";


export function Navbar() {
    var [chk, func] = useState(true)
    var [cartShow, showFunc] = useState(false);
    var [cartProds, ProdsFunc] = useState([]);
    var [totalCount, CountFunc] = useState(0);
    useEffect(() => {
        store.subscribe(() => {
            cartFunc(store.getState().cart)
        })
        const know = store.getState().cart;
        if (know.length > 0) {
            func(false);
            ProdsFunc(know);
            var total = 0;
            know.forEach(element => {
                total += Number(element.quant);
            });
            CountFunc(total);
        }
    }, [])
    function show(get) {
        showFunc(get);
    }
    function cartFunc(state) {
        if (state.length !== 0) {
            func(false)
        } else {
            func(true)
        }
        var total = 0;
        state.forEach(element => {
            total += Number(element.quant);
        });
        CountFunc(total);
        ProdsFunc(state);
    }
    return (
        <nav className="navbar navbar-expand-lg navbar-light py-3 px-3">
            <div className="container-fluid">
                <img src="img/home/furniture-logo.png" className="navbar-img" alt="" />
                <Link className="text-dark text-decoration-none fw-bold h3 ms-2 mt-2" to={'/'}>Funiture</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ms-5 ps-3 mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className="nav-link me-3" aria-current="page" to={'/'}>Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link me-3" aria-current="page" to={'/shop'}>Shop</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link me-3" aria-current="page"
                                to={'/category/Living%20Room/Living%20Room'}>
                                Living Room
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link me-3" aria-current="page" to={'/category/Kitchen/Kitchen'}>Kitchen</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link me-3" aria-current="page" to={'/'}>Decoration</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link me-3" aria-current="page" to={'/'}>Blog</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link me-3" aria-current="page" to={'/'}>Contact</Link>
                        </li>


                    </ul>
                    <form className="d-flex ms-auto position-relative">
                        <i className="fa-solid fa-magnifying-glass mt-2 me-4 fa-lg text-dark"></i>
                        <i
                            className="fa-solid fa-cart-shopping mt-2 me-2 fa-lg text-dark pointer"
                            onMouseOver={() => { show(true) }}
                            onMouseOut={() => { show(false) }}
                        ></i>
                        <span className={`rounded-pill bg-danger text-light position-absolute cart-quant px-1`}>
                            {totalCount}
                        </span>
                    </form>
                    {
                        (cartShow) &&
                        <>
                            {
                                (chk) ?
                                    <div
                                        className="position-absolute end-0 me-4 ht-md top-50 mt-2 col-3 shadow bg-white rounded-10 z-10"
                                        onMouseOver={() => { show(true) }}
                                        onMouseOut={() => { show(false) }}
                                    >
                                        <img src="img/home/cart-bag.jpg" className="navbar-cart-img img-center mt-3" alt="" />
                                        <p className="text-center fw-bold mt-3 text-md">NO Product in Cart</p>
                                    </div>
                                    :
                                    <div
                                        className="position-absolute end-0 me-4 top-50 mt-2 col-3 shadow bg-white rounded-10 px-3 w-25 z-10"
                                        onMouseOver={() => { show(true) }}
                                        onMouseOut={() => { show(false) }}
                                    >
                                        <CartProdsFunc put={cartProds} />
                                        <hr />
                                        <Link to={'/viewCart'}>
                                            <button className="btn text-dark border w-100">
                                                View Cart
                                            </button>
                                        </Link>
                                        <button className="btn btn-danger w-100 mb-3 mt-2">Checkout</button>
                                    </div>
                            }
                        </>
                    }
                </div>
            </div>
        </nav>
    )
}

function CartProdsFunc(get) {
    get = get.put;
    function dlt(e) {
        const know = get.find(value => value._id === e.target.id)
        store.dispatch(Delete(know))
    }
    function show(props) {
        return (
            <div className="mb-0" key={props._id}>
                <div className="row g-0">
                    <div className="col-md-3">
                        <img src={props.filename} className="rounded-3 img-fluid rounded-start add-cart-img img-center mt-2" alt="..." />
                    </div>
                    <div className="col-md-8">
                        <div className="card-body">
                            <h6 className="card-title sans fw-bold mb-0">{props.name}</h6>
                            <span className="text-md w-bold">{props.quant}</span>
                            <span className="text-md w-bold mx-1">x</span>
                            <span className="text-md w-bold">{props.price}</span>
                        </div>
                    </div>
                    <div className="col">
                        <i
                            id={props._id}
                            className="fa-solid fa-xmark mt-4 pointer"
                            onClick={dlt}
                        >
                        </i>
                    </div>
                </div>
            </div>
        )
    }

    return get.map(show)
}

